import React from 'react';
import PlatformContext from '../../context/platform';

import './header-mobile.scss';
import './header-pc.scss';
import logo from '../../assets/images/logo.svg';
import logoM from '../../assets/images/logo_m.svg';


function Header() {
    const platform = React.useContext(PlatformContext);

    return (
        <header id="hader" className="header">
            <div className="header--content_wrapper">
                <div className="header--content_wrapper--content">
                    <img src={platform === 'pc' ? logo : logoM} alt="Stopol Auto" className="header--content_wrapper--content--logo"/>
                    <span className="header--content_wrapper--content--text">
                        наш сайт <br/>
                        скоро будет готов!
                    </span>
                </div>
            </div>
        </header>
    );
};

export default Header;