import React from 'react';
import Fader from '../fader';
import './static-mobile.scss';
import './static-pc.scss';

import sun from '../../assets/images/emoji_sun.png';
import car from '../../assets/images/emoji_car.png';
import clock from '../../assets/images/emoji_clock.png';

function StaticSection() {

    const [currSlide, setCurrSlide] = React.useState(0);

    return (
        <section id="static_section" className="static_section">
            <div className="static_section--block_0 static_section--block">
                <h1>
                    МИР УМНЫХ ТЕХНОЛОГИЙ&nbsp;
                    <span>
                        В вашем <br/>
                        автомобиле
                    </span>
                </h1>
                <p>
                    Полезные сервисы и развлечения <br/> 
                    в едином интерфейсе
                </p>
            </div>
            <div className="static_section--block_1 static_section--block">
                <h2>Преимущества</h2>
                <Fader slide={currSlide} className="static_section--block_1--fader">
                    <p>
                        Безопасность и комфорт в управлении <br/>
                        системами авто во время вождения
                    </p>
                    <p>
                        Уникальные функции и доступ  <br/>
                        к цифровым сервисам
                    </p>
                    <p>
                        Экономия времени и средств <br/>
                        при использовании автомобиля
                    </p>
                </Fader>
                <div className="static_section--block_1--buttons">
                    <button 
                        onClick={() => setCurrSlide(0)}
                        className={currSlide === 0 && 'active'}
                    >
                        <img src={sun} alt="Комфорт"/>
                    </button>
                    <button 
                        onClick={() => setCurrSlide(1)}
                        className={currSlide === 1 && 'active'}
                    >
                        <img src={car} alt="Функции"/>
                    </button>
                    <button 
                        onClick={() => setCurrSlide(2)}
                        className={currSlide === 2 && 'active'}
                    >
                        <img src={clock} alt="Ресурсы"/>
                    </button>
                </div>
            </div>
            <div className="static_section--block_2 static_section--block">
                <h1>
                    <span>
                        СВЯЖИТЕСЬ <br/>
                        С НАМИ
                    </span>
                </h1>
                <p>
                    По всем вопросам обращайтесь на:
                    <a href="mailto: offers@Stopol-auto.com">offers@Stopol-auto.com</a>
                </p>
            </div>
    
            <span className="static_section--copyright">Stopol Auto 2020 все права защищены</span>
        </section>
    );
}

export default StaticSection;