import React from "react"
import Header from '../components/header/index.js';
import StaticSection from '../components/static/index.js'; 
import PlatformContext from '../context/platform';
import Gallery from '../components/gallery/index.js';

function IndexPage() {
  const [platform, setPlatform] = React.useState(null);

  function watchPlatform() {
      function determinePlatform() {
          if ( !document.documentElement.clientWidth ) return;
          else if ( document.documentElement.clientWidth >= 1024 ) setPlatform('pc');
          else setPlatform('mobile');
      }

      determinePlatform();
      window.addEventListener('resize', determinePlatform);
      return () => window.removeEventListener('resize', determinePlatform);
  }

  React.useEffect(watchPlatform, []);

  return (
    <PlatformContext.Provider value={platform}>
      <Header/>
      <main>
        <StaticSection/>
        <Gallery />
      </main>
    </PlatformContext.Provider>
  );
}

export default IndexPage
