import React from 'react';
import Menu from './menu.js';
import Animation from './animation.js';
import Fader from '../fader.js';
import './styles/index-pc.scss';
import './styles/index-mobile.scss';

import cloud from '../../assets/images/emoji_cloud.png';
import bolt from '../../assets/images/emoji_bolt.png';
import man from '../../assets/images/emoji_man.png';
import phone from '../../assets/images/emoji_phone.png';

const animationSegments = [
    {
        start: [130, 250]
    },
    {
        start: [0, 120]
    },
    {
        start: [260, 380]
    }
]

function Gallery() {
    const [currSegment, setCurrSegment] = React.useState(0);
    
    React.useEffect(() => setCurrSegment(2), []);

    return (
        <section id="gallery_section" className="gallery_section">
            <Menu callback={i => setCurrSegment(i)}/>
            <Animation segment={animationSegments[currSegment].start}/>
            <Fader slide={currSegment} className="gallery_section--fader">
                <>
                    <h3>Приложение – компаньон</h3>
                    <p>
                        <span>
                            <img src={phone} alt="Смартфон"/> Для удобного управления системами <br/>
                            прямо с телефона
                        </span>
                    </p>
                </>
                <>
                    <h3>
                        Мультимедийная <br/>
                        система с МТС АВТО
                    </h3>
                    <p>
                        <span><img src={bolt} alt="Технологии"/> Современные технологии</span>
                        <span><img src={man} alt="Универсальность"/> Персонализация данных</span>
                    </p>
                </>
                <>
                    <h3>Видеорегистратор</h3>
                    <p>
                        <span><img src={cloud} alt="Облако"/> С функцией облачного хранения данных</span>
                    </p>
                </>
            </Fader>
        </section>
    );
}

export default Gallery;