import React from 'react';

function Fader({ 
    children, 
    slide = 0, 
    duration = 600, 
    id, 
    className 
}) {

    const 
    [isVisible, setIsVisible] = React.useState(true),
    [currSlide, setCurrSlide] = React.useState(0),
    timer = React.useRef();

    function goToslide(index) {
        if ( index < 0 || index > children.length - 1 ) return;

        setIsVisible(false);
        timer.current = setTimeout(() => setCurrSlide(index), duration/2);
    }

    React.useEffect(() => {
        goToslide(slide);
        return () => {
            clearTimeout(timer.current);
        }
    }, [slide]);
    React.useEffect(() => { setIsVisible(true); }, [currSlide]);

    return (
        <div 
            id={id} 
            className={`fader ${className ?? ''}`}
            style={{
                opacity: isVisible ? 1 : 0,
                transition: `opacity ${duration/2}ms`
            }}
        >
            {Array.isArray(children) ? children[currSlide] : children}
        </div>
    );
}

export default Fader;